import React, { useState, useContext, useMemo } from "react";
import { Button, Checkbox, Divider, FormControl, FormControlLabel, FormGroup, FormLabel, IconButton, InputLabel, NativeSelect, Paper, Radio, RadioGroup, Stack, TextField, Tooltip, Typography } from "@mui/material"
import { useForm } from "react-hook-form";
import { STYLE_2 } from "../components/const";
import DatePicker from "react-datepicker";

import DateRangeOutlinedIcon from '@mui/icons-material/DateRangeOutlined';
import { ConfigContext } from "../contexts/config";
import { formattedDate } from "../lib/calcDate";
import { sendOrgParams } from "../apis/config";
import { SnackbarContext } from "../contexts/snackBar";


export const Setting = () => {
   
  const orgConfig = useContext(ConfigContext);
  const sb = useContext(SnackbarContext);
  const colors=  
  [ 
    {"key":"red","value": orgConfig.params.red},
    {"key":"royalblue","value": orgConfig.params.royalblue},
    {"key":"green","value": orgConfig.params.green},
    {"key":"orange","value": orgConfig.params.orange},
  ]
    
  const initialDate = {
    submittable_start: new Date(orgConfig.params.submittable_start),
    submittable_end: new Date(orgConfig.params.submittable_end), 
  }  
  const month = useMemo(()=>([...Array(29)].map((_,i)=>`${i +1}`)),[]);
  const hours = useMemo(()=>([...Array(25).keys()]),[]);
  const [date, setDate] = useState(initialDate);
  const [event, setEvent] = useState({})
 
  
  const { register, handleSubmit, formState: { errors } } = useForm();

  const handleChange = (e) => {
    setEvent({
      ...event,
      [e.target.name]: e.target.value
   })
    console.log(event)
  }

   

  const onSubmit = (params) => {
    sendOrgParams({...params, ...date, ...event})
    .then(res => {
      if(res.status !== 200){return}
      orgConfig.setParams({
        ...res.data.config,
        orgName: res.data.orgName,
        orgAddress: res.data.orgAddress
      })
      sb.setSnackBar({open: true, variant: "success", content: ""})

    })
  }

    return (
      <Paper sx={{p:1, ...STYLE_2}}>
        <form onSubmit={handleSubmit(onSubmit)}>
        <Stack 
          sx={{py:2}}
          direction="row" 
          alignItems="center"
          spacing={2} 
          justifyContent="end"
          >
          <Typography varinat="h6"> {orgConfig.params.name} </Typography>
          <TextField 
             {...register('name',{ required: ""})} 
            label="" 
            className="mui-input"
            defaultValue={orgConfig.params.orgName}
          />
        </Stack>
        <Stack>
        <TextField 
         {...register('address',{ required: ""})} 
          fullWidth 
          sx={{my:1, maxWidth: 400, marginLeft: "auto"}}
          label=""
          className="mui-input"
          defaultValue={orgConfig.params.orgAddress}
          />
        </Stack>
       
        <Stack 
          direction="row" 
          spacing={2} 
          alignItems="center"
          justifyContent="space-between"
        >
        <Tooltip title="">
          <Typography variant="subtitle1"></Typography>
        </Tooltip>
        <div>
          <FormControl sx={{m:1, width:50}} >
          <InputLabel></InputLabel>
              <NativeSelect
                 {...register('open',{ required: ""})} 
                className="mui-input"
                label=""
                defaultValue={orgConfig.params.open}
              > 
              {hours.map(n => 
                  <option key={n} value={n}>{n}</option>
                  )
              }   
              </NativeSelect>
              </FormControl>
              <FormControl sx={{ m: 1, width:50}} >
              <InputLabel></InputLabel>
              <NativeSelect
                {...register('close',{ required: ""})} 
                className="mui-input"
                label=""
                defaultValue={orgConfig.params.close}
              > 
              {hours.map(n => 
                  <option key={n} value={n}>{n}</option>
                  )
              }   
              </NativeSelect>
            </FormControl>

        </div>
        </Stack>

        <Divider sx={{my:1}} />

      <Typography variant="h6"></Typography>
      <Stack
          direction="row" 
          spacing={2} 
          alignItems="center"
          justifyContent="end"
          >
        <Tooltip title="ON">
          <FormGroup>
            <FormControlLabel 
              control=
              {<Checkbox
                {...register('chat_notice')} 
                 checked={orgConfig.params.chat_notice}
                 />}
              label=""
              labelPlacement="start"
            />
          </FormGroup>
          </Tooltip>
       
        </Stack>
     
        
        <Divider sx={{my:1}} />

        <Typography variant="h6"></Typography>
        <Stack
         direction="row" 
         spacing={2} 
         alignItems="center"
         justifyContent="space-between"
        >
        
          <Typography variant="subtile1"></Typography>
          <div>
       
          <FormControl sx={{ m: 1, width:50}}>
          <InputLabel></InputLabel>
              <NativeSelect
                {...register('min_work_time')} 
                className="mui-input"
                label=""
                defaultValue={orgConfig.params.min_work_time}
              > 
              {hours.map(n => 
                  <option key={n} value={n}>{n}</option>
                  )
              }   
              </NativeSelect>
          </FormControl>
          </div>
        </Stack>
        <Typography variant="subtile1"></Typography>
       
        <Stack direction="row" spacing={2} alignItems="center" justifyContent="end">
      
          <FormControl sx={{width:100}}>
           
          {/* <IconButton> */}
          <Typography variant="h6"></Typography>
          <NativeSelect
                {...register('min_work_time')} 
                className="mui-input"
                label=""
                defaultValue={orgConfig.params.shift_start_day || 1}
              > 
              {month.map(n => 
                  <option key={n} value={n}>{n}</option>
                  )
              }   
              </NativeSelect>

          {/* </IconButton> */}
          
          </FormControl>

          
          {/* <Typography variant="h6"></Typography>
          <FormControl sx={{ width:100}}>
    
          <IconButton>
          <NativeSelect
                {...register('min_work_time')} 
                className="mui-input"
                label=""
                defaultValue={orgConfig.params.span}
              > 
              {month.map(n => 
                  <option key={n} value={n}>{n}</option>
                  )
              }   
              </NativeSelect>

          </IconButton>
          
          </FormControl>
          */}
        </Stack>
        <Divider sx={{my:2}} />
        <Typography variant="h6"></Typography>
        <Stack 
          sx={{py:2}}
          direction="row" 
          alignItems="center"
          spacing={2} 
          justifyContent="space-between"
          >
        <Tooltip title="">
          <Typography variant="subtitle1"></Typography>
        </Tooltip>
          <TextField 
            {...register('stampable_distance')} 
            label="m" 
            className="mui-input"
            type="number"
            defaultValue={orgConfig.params.stampable_distance}
          />
        </Stack>
  
        <Divider sx={{my:2}} />
        <Typography variant="h6"></Typography>
        <Stack 
          sx={{py:2}}
          direction="row" 
          justifyContent="space-between"
        >
        <Tooltip title="">
          <Typography variant="subtitle1"></Typography>
        </Tooltip>
        <div>
          <FormControl>
            <RadioGroup row >
            {colors.map(c =>  
            <div key={c.key}>
              <TextField  
                style={{width:80, backgroundColor:"white"}}
                size="small"
                name={c.key}
                defaultValue={c.value}
                onChange={handleChange}
              />
           
              <FormControlLabel
              key={c.key}
                value={c.key} 
                control={
                <Radio
                  sx={{color: c.key}}
                  />} 
              />  
            </div>
            )}
          
            </RadioGroup>
          </FormControl>
        </div>
       
      </Stack>
      <Stack sx={{my:2}}>
      <Button 
          fullWidth
          type="submit"
          style={{margin:"0 auto", maxWidth:300}}
          variant="contained"
          color="success"
        >
          
        </Button>
       

      </Stack>
    
       
        </form>
      </Paper>
    )

}